@import "mixins/font-face";
// +font-face("RobotoLight", "/fonts/RobotoLight/RobotoLight")
// +font-face("RobotoBold", "/fonts/RobotoBold/RobotoBold")
// +font-face("RobotoBlack", "/fonts/RobotoBlack/RobotoBlack")
@include font-face("Roboto", "/fonts/roboto-regular");
@include font-face("RobotoBold", "/fonts/roboto-bold");
@include font-face("RobotoLight", "/fonts/Roboto-Light");
@include font-face("Font Awesome 5 Free", "../webfonts/fa-regular-400");
@include font-face("Font Awesome 5 Solid", "../webfonts/fa-solid-400");
@include font-face("Font Awesome 5 Brand", "../webfonts/fa-brands-400");
@include font-face("Glyphicons Halflings", "../fonts/glyphicons-halflings-regular");

@mixin ff-roboto() {
  font-family: Roboto, Arial, sans-serif;
  font-weight: 400; }

@mixin ff-roboto-light() {
  font-family: RobotoLight, Arial, sans-serif;
  font-weight: 400; }

@mixin ff-roboto-bold() {
  font-family: RobotoBold, Arial, sans-serif;
  font-weight: 700; }

@mixin ff-arial() {
  font-family: Arial, sans-serif;
  font-weight: 400; }

@mixin ff-arial-bold() {
  font-family: Arial, sans-serif;
  font-weight: 700; }

